import {
  Controller,
  ContextMenuItem,
} from '@ali/4ever-cangjie';
import { getCurrentItem } from './utils';
import { restartNumbering, continueNumbering } from './actions';

export default function createContextMenu() {
  return function contextMenuPlugin(pluginConfig) {
    const locale = pluginConfig.locale || {};
    return function contextMenu(
      controller: Controller,
      next: () => ContextMenuItem[] | null,
    ) {
      const menus: ContextMenuItem[] = [];
      const { selection } = controller.value;
      if (selection?.isCollapsed) {
        const currentItem = getCurrentItem(controller)[0];
        const listConfig = currentItem?.data?.list;
        if (listConfig && listConfig.isOrdered) { // 有序列表才有下列选项
          menus.push({
            name: locale.restartNumbering,
            action: restartNumbering(),
            key: 'list-restart-numbering',
            options: {
              mobile: false,
              group: 2,
            },
          }, {
            name: locale.continueNumbering,
            action: continueNumbering(),
            key: 'list-continue-numbering',
            options: {
              mobile: false,
              group: 2,
            },
          });
        }
      }
      const others = next() || [];
      return menus.concat(others);
    };
  };
}
